import React from 'react';

const SelectPicker = ({className, key, refs, name, value, onChange, options,}) => {

  return (
    <select
      key={key}
      ref={refs}
      className={`w-full h-10 px-4 py-1 overflow-auto border-2 border-solid rounded-lg resize-none border-gray bg-dark-secondary focus:outline-none ${className}`}
      name = { name }
      defaultValue = {value}
      onChange = { onChange } 
    >
      <option className='px-2 py-2 cursor-pointer' key={`${name}-picker`} value={""}>Select...</option>
      {options && options.map( ( item, index ) => {
        return <option className='px-2 py-2 cursor-pointer' key={`${item}-${index}-${name}`} value={item.value}>{item.text}</option>
      })}
    </select>
  )

}

export default SelectPicker;