import React from "react";

const Tag = ({isRemovible = true, classNames, onRemove, onClick, text, closeStyle}) => {
  return (
    <div
      onClick={onClick} 
      className={`w-fit flex flex-row items-center h-fit py-1 px-3 text-white rounded-full bg-primary-dark roboto-font border-primary-dark ${classNames}`}>
      {text}
      {isRemovible &&
        <button onClick={ (e) => onRemove(e) } className={`cursor-pointer ml-2 focus:outline-none ${closeStyle}`}>
          <svg xmlns="http://www.w3.org/2000/svg" className="w-8 h-8 md:w-6 md:h-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
          </svg>
        </button>
      }
    </div>
  );
}

export default Tag; 