import React, { useEffect, useRef, useState } from "react";
import ReactDatePicker from "react-datepicker";

import { ReactComponent as HomeIllustration } from "../../assets/welcome.svg";
import "../../index.css";
import "react-datepicker/dist/react-datepicker.css";
import { useForm } from "../../utils/useForm";
import Message from "../../components/message/Message";
import Input from "../../components/input/Input";

import LogoHeader from "../../components/logo-header/LogoHeader";
import Card from "../../components/card/Card";
import SelectPicker from "../../components/select-picker/SelectPicker";
import axios from "../../config/axios";
import { useHistory } from "react-router-dom";
import Avatar from "../../components/avatar/Avatar";
import TextArea from "../../components/textarea/TextArea";
import Tag from "../../components/tag/Tag";

const initialForm = {
  name: "",
  lastname: "",
  username: "",
  email: "",
  password: "",
  confirmPassword: "",
  esperanto_level: "",
  gender: "",
  nationality: "",
  birthday: "",
  profile_picture: "",
  biography: "",
  native_language: "",
  residence: "",
};

const validationsForms = (form, page) => {
  let errors = {};
  let regexName = /^[A-Za-z\s]+$/;
  let regexUsername = /^.{5,12}$/;
  let regexEmail = /^(\w+[/./-]?){1,}@[a-z]+[/.]\w{2,}$/;
  let regexPassword = /^(?=\w*\d)(?=\w*[A-Z])(?=\w*[a-z])\S{8,16}$/;
  let regexDate = /^\d{4}\-(0?[1-9]|1[012])\-(0?[1-9]|[12][0-9]|3[01])$/

  if (!form.name.trim()) {
    errors.name = "";
  }else if (!regexName.test(form.name.trim())){
    errors.name = "Only letters and blank spaces are accepted";
  }

  if (!form.lastname.trim()) {
    errors.lastname = "";
  }else if (!regexName.test(form.lastname.trim())){
    errors.lastname = "Only letters and blank spaces are accepted";
  }

  if (!form.username.trim()) {
    errors.username = "";
  }else if (!regexUsername.test(form.username.trim())){
    errors.username = "The username must have between 5 and 12 characters";
  }

  if (!form.email.trim()) {
    errors.email = "";
  }else if (!regexEmail.test(form.email.trim())){
    errors.email = "Enter a email";
  }

  if (!form.password.trim()) {
    errors.password = "";
  }else if (!regexPassword.test(form.password.trim())){
    errors.password = "The password must have between 8 and 16 characters, at least one digit, at least one lower case and at least one upper case.";
  }

  if(form.password !== form.confirmPassword){
    errors.confirmPassword = "The passwords should match each other"
  }

  if(page === 2)
  {
    if(form.esperanto_level === ""){
      errors.esperanto_level = "This field is required"
    }
  
    if(!form.birthday.trim()) {
      errors.birthday = "";
    }else if (!regexDate.test(form.birthday.trim())){
      errors.birthday = "Enter a valid date in formta yyyy-mm-dd";
    }
  
    if(form.gender === ""){
      errors.gender = "This field is required"
    }
  
    if(form.nationality === ""){
      errors.nationality = "This field is required"
    }
  }


  return errors;
};

function Register() {
  const {
    form,
    errors,
    loading,
    response,
    handleChange,
    handleBlur,
    handleSubmit,
  } = useForm(initialForm, validationsForms);

  const [languages, setLanguages] = useState([
    {
      value: 'Spanish', 
      text: 'Spanish'
    }, 
    {
      value:'English', 
      text: 'English'
    }, 
    {
      value:'Japanese', 
      text: 'Japanese'
    } 
  ]);
  const [filteredLanguages, setFilteredLanguages] = useState([...languages]);
  const [formPage, setFormPage] = useState(1);
  const [interests, setInterests] = useState([]);
  const [currentInterest, setCurrentInterest] = useState("");
  const [spokenLanguages, setSpokenLanguages] = useState([""]);

  const [contactInfo,setContactInfo] = useState({
    facebook: '',
    instagram: '',
    twitter: ''
  })

  //Effects
  useEffect(() => {
    setFilteredLanguages(languages.filter( item => item.value !== form.native_language));
  }, [form.native_language]);

  //Refs
  const interestRef = useRef(null);
  
  const history = useHistory();

  const convertBase64 = (file) => {
    return new Promise((resolve, reject) => {
      const fileReader = new FileReader();
      fileReader.readAsDataURL(file);
      fileReader.onload = () => {
        resolve(fileReader.result);
      }
      fileReader.onerror = (error) => {
        reject(error);
      }
    })
  }

  const handleFileRead = async (e) => {
    const file = e.target.files[0];
    const base64 = await convertBase64(file);
    handleChange({target: {name: 'profile_picture', value: base64}});
  } 

  const addInterest = () => {
    let interestList = [...interests];
    interestList.push(currentInterest);
    setInterests(interestList);
    console.log(interestRef.current);
  }

  const updateLanguages = (value,  assert) => {
    const newList = [...filteredLanguages];
    let filtered = newList.filter(item => item.value !== value);
    if(form.native_language !== "" && assert)
    {
      filtered.push({text: value, value: value});
    }
    setFilteredLanguages(filtered);
  }

  const addSpokenLanguage = (val) => {
    updateLanguages(val, false);
    let languageList = [...spokenLanguages];
    languageList.push(val);
    setSpokenLanguages(languageList);
  }

  const handleFormPages = (type) => {
    console.log(type);
    let pageValue = formPage;
    if(type === 'next')
    {
      pageValue = pageValue + 1;
    }
    else
    {
      pageValue = pageValue - 1;
    }
    setFormPage(pageValue);
  }

  const signUp = async (data) => {
    let dataToSend = {...data};
    let inters = new Set(interests);
    dataToSend.interests = [...inters];
    dataToSend.spoken_languages = spokenLanguages;
    dataToSend.contact = JSON.stringify(contactInfo);

    console.log(dataToSend);

    const response = await axios.post('/auth/signup',{...dataToSend, registration_date: Date.now(), last_connection: Date.now(), status: "offline"})
    console.log(response);
    if(response.data.email)
    {
      history.replace('/login');
    }
  }

  return (
    <div className="flex flex-col w-full h-full px-3 md:h-screen md:px-24 bg-dark">
      
      <LogoHeader/>

      <div className="grid content-center justify-center h-auto grid-cols-2 pt-3 my-10 md:my-20 md:pt-0">
        
        <div className="flex justify-center order-1 w-full h-full col-span-2 mb-8 md:col-span-1 md:order-2 md:mb-0">
          <HomeIllustration className="hidden object-none object-center w-full h-full md:w-11/12 md:block" />
        </div>

        <div className="flex flex-col order-2 col-span-2 md:col-span-1 md:order-1 md:px-5">

          <Card classNames="p-8 roboto-font bg-dark-tertiary rounded-lg">
            
              <h1 className="mb-3 text-2xl font-bold leading-snug text-center text-white md:text-center md:leading-snug md:text-2xl">
                {formPage === 1 ? "Welcome!" : formPage === 3 ? `This it's up to you` : `Fill this information too, please`}
                <font className="text-primary">.</font>
              </h1>

              <p className="mb-5 text-lg leading-normal text-center md:leading-normal md:text center md:text-2z text-gray-light">
                <span className="">
                { formPage === 1 
                ?
                  `Welcome to the community. Please enter your data to set up
                   your profile.`
                : formPage === 3 &&
                  `You can fill this fields later updating your profile information.`
                }
                </span>
              </p>
            
            <form onSubmit={(e) => handleSubmit(e, formPage, signUp)}>
              { formPage === 1 &&
                <div className="text-white borderButton">
                  <p className="my-2 text-left text-white">Name</p>
                  <Input
                    classNames="text-center w-full"
                    placeholder="Enter your first name"
                    type="text"
                    name="name"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={form.name}
                  />
                  {errors.name && (
                    <p className="text-danger">{errors.name}</p>
                  )}

                  <p className="my-2 text-left text-white">Last name</p>
                  <Input
                    classNames="text-center w-full"
                    placeholder="Enter your last name"
                    type="text"
                    name="lastname"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={form.lastname}
                  />
                  {errors.lastname && (
                    <p className="text-danger">{errors.lastname}</p>
                  )}

                  <p className="my-2 text-left text-white">User name</p>
                  <Input
                    classNames="text-center w-full"
                    placeholder="Set your user name"
                    type="text"
                    name="username"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={form.username}
                  />
                  {errors.username && (
                    <p className="text-danger">{errors.username}</p>
                  )}

                  <p className="my-2 text-left text-white">Email</p>
                  <Input
                    classNames="text-center w-full"
                    placeholder="Enter your email"
                    type="email"
                    name="email"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={form.email}
                  />
                  {errors.email && (
                    <p className="text-danger">{errors.email}</p>
                  )}

                  <p className="my-2 text-left text-white">Password</p>
                  <Input
                    classNames="text-center w-full"
                    placeholder="Enter your password"
                    type="password"
                    name="password"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={form.password}
                  />
                  {errors.password && (
                    <p className="text-danger">{errors.password}</p>
                  )}

                  <p className="my-2 text-left text-white">Confirm Password</p>
                  <Input
                    classNames="text-center w-full"
                    placeholder="Enter your password"
                    type="password"
                    name="confirmPassword"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={form.confirmPassword}
                  />
                  {errors.confirmPassword && (
                    <p className="text-danger">{errors.confirmPassword}</p>
                  )}
                </div>
              }
              { formPage === 2 &&
                <div className="text-white borderButton">
                  <p className="my-2 text-left text-white">Gender</p>
                  <SelectPicker
                    options={[{value: 'Male', text: 'Male'}, {value:'Female', text: 'Female'}]}
                    name='gender'
                    onChange={handleChange}
                    value={form.gender} 
                  />
                  {errors.gender && (
                    <p className="text-danger">{errors.gender}</p>
                  )}

                  <p className="my-2 text-left text-white">Nationality</p>
                  <SelectPicker
                    options={[{value: 'Mexican', text: 'Mexican'}, {value:'American', text: 'American'}]}
                    name='nationality'
                    onChange={handleChange}
                    value={form.nationality} 
                  />
                  {errors.nationality && (
                    <p className="text-danger">{errors.nationality}</p>
                  )}

                  <p className="my-2 text-left text-white">Residence</p>
                  <Input
                    classNames="text-start w-full"
                    placeholder="The place you currently live"
                    type="text"
                    name="residence"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={form.residence}
                  />

                  <p className="my-2 text-left text-white">Birthday</p>
                  <ReactDatePicker
                    showTwoColumnMonthYearPicker
                    showYearDropdown
                    className="w-full h-10 px-4 py-1 overflow-auto border-2 border-solid rounded-lg resize-none border-gray bg-dark-secondary focus:outline-none"
                    placeholderText="Select a date..." 
                    selected={form.birthday}
                    onChange={(date) => handleChange({target: {name: 'birthday', value: date}})}
                  />
                  {errors.birthday && (
                    <p className="text-danger">{errors.birthday}</p>
                  )}
                </div>
              }
              { formPage === 3 &&
                <div className="text-white borderButton">
                  <p className="my-2 text-left text-white">Esperanto Level</p>
                  <SelectPicker
                    options={[{value: 'Basic', text: 'Basic'}, {value:'Middle', text: 'Middle'}, {value:'Advanced', text: 'Advanced'} ]}
                    name='esperanto_level'
                    onChange={handleChange}
                    value={form.esperanto_level} 
                  />
                  {errors.esperanto_level && (
                    <p className="text-danger">{errors.esperanto_level}</p>
                  )}
                  
                  <p className="my-2 text-left text-white">Native Language</p>
                  <SelectPicker
                    options={languages}
                    name='native_language'
                    onChange={(e) => {
                      handleChange(e);
                      updateLanguages(e.target.value, true);
                    }}
                    value={form.native_language} 
                  />

                  <p className="my-2 text-left text-white">Spoken Languages</p>
                  <div className="flex flex-wrap w-full my-3 h-fit">
                    {spokenLanguages.map((item, index) => {
                      return (
                        <div className="flex flex-row items-center w-full mt-3">
                          <SelectPicker
                            name={`picker-${index}`}
                            options={filteredLanguages}
                            value={spokenLanguages[index]}
                            onChange={(e) => {
                              let temp = [...spokenLanguages];
                              temp[index] = e.target.value;
                              setSpokenLanguages(temp);
                            }}
                          />
                          { index > 0 &&
                            <button
                              key={`picker-${index}`} 
                              onClick = {(e) => {
                                e.preventDefault();
                                let temp = spokenLanguages.filter(val => val !== item);
                                if(temp.length === 0)
                                {
                                  temp.push("");
                                }
                                setSpokenLanguages(temp);
                                console.log(temp);
                              }}
                              className={`cursor-pointer ml-2 focus:outline-none`}
                            >
                              <svg xmlns="http://www.w3.org/2000/svg" className="w-8 h-8 md:w-6 md:h-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                              </svg>
                            </button>
                          }
                        </div>
                      );
                    })
                    }
                    <button
                      onClick={(e) => {
                        e.preventDefault();
                        let temp = [...spokenLanguages];
                        temp.push("");
                        setSpokenLanguages(temp);
                      }}
                      className="px-3 py-1 mt-3 text-white rounded-lg w-fit bg-primary hover:bg-primary-dark roboto-font border-primary hover:border-primary-dark ballon-right"
                    >
                      Add one more
                    </button>
                  </div>
                </div>
              }
              { formPage === 4 &&
                <div className="text-white borderButton">
                  <div className="flex flex-col justify-start md:flex-row">
                    <div className="flex flex-col items-center w-full p-3 md:w-6/12">
                      <Avatar src={form.profile_picture} gender = {form.gender} classNames='w-32 h-32 border-0' sizes={'w-32 h-32'}/>
                      <label className="px-3 py-2 mt-2 text-white rounded-lg cursor-pointer w-fit hover:bg-dark-secondary bg-gray hover:border-dark-tertiary border-gray roboto-font ballon-right">
                        <input
                          className="hidden"
                          type="file"
                          name="profile_picture"
                          onChange={handleFileRead} 
                        />

                        Add Photo
                      </label>
                    </div>
                    <div className="w-full mt-3 md:mt-0">
                      <div className="flex flex-row justify-between w-full">
                        <p className="my-2 text-left text-white">Biography</p>
                        <p className="my-2 text-left text-white">{`${100 - form.biography.length}/100`}</p>
                      </div>
                      <TextArea
                        classNames="text-start w-full resize-none h-24"
                        placeholder="Tell us about you..."
                        type="text"
                        name="biography"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={form.biography}
                        maxLength = {100}
                        rows = {5}
                      />
                    </div>
                  </div>
                  <p className="my-2 text-left text-white">Interests</p>
                  <div className="flex flex-col justify-start md:flex-row md:">
                    <Input
                      refs = {interestRef}
                      classNames="text-center w-full"
                      type="text"
                      onKeyDown={(e) => {
                        if(e.key === "Enter")
                        {
                          e.preventDefault();
                          addInterest();
                          interestRef.current.value = "";
                        }
                      }}
                      onBlur={(e) => setCurrentInterest(e.target.value)} 
                      onChange={(e) => setCurrentInterest(e.target.value)}
                      value={currentInterest}
                    />
                    <button
                      onClick={(e) => {
                        e.preventDefault();
                        addInterest();
                        interestRef.current.value = "";
                      }}
                      className="px-3 py-1 py-2 my-2 ml-2 text-white rounded-lg md:mx-2 md:my-0 w-fit bg-primary hover:bg-primary-dark roboto-font border-primary hover:border-primary-dark ballon-right"
                    >
                      Add
                    </button>
                  </div>
                  <div className="flex flex-wrap w-full my-3 h-fit">
                    {interests.map((item, index) => {
                      return (
                        <Tag
                          key={`${item}-${index}`} 
                          classNames={"m-2 w-fit"} 
                          isRemovible={true} 
                          text={item} 
                          onRemove = {(e) => {
                            e.preventDefault();
                            setInterests(interests.filter(val => val !== item))
                          }}
                        />
                      )
                    })
                    }
                  </div>
                  <p className="my-2 text-left text-white">Contacto</p>
                  <div className="flex flex-row items-center justify-start mt-2 ml-5">
                    <p className="italic text-left">Facebook</p>
                    <Input
                      classNames="text-start w-full ml-2 border-r-0 border-l-0 border-t-0 bg-dark-tertiary !rounded-none"
                      placeholder="Facebook link"
                      type="text"
                      name="facebook"
                      onBlur={(e) => setContactInfo({...contactInfo, facebook: e.target.value})}
                      onChange={(e) => setContactInfo({...contactInfo, facebook: e.target.value})}
                      value={contactInfo.facebook}
                    />
                  </div>
                  <div className="flex flex-row items-center justify-start mt-2 ml-5">
                    <p className="italic text-left">Twitter</p>
                    <Input
                      classNames="text-start w-full ml-2 border-r-0 border-l-0 border-t-0 bg-dark-tertiary !rounded-none"
                      placeholder="Twitter link"
                      type="text"
                      name="twitter"
                      onBlur={(e) => setContactInfo({...contactInfo, twitter: e.target.value})}
                      onChange={(e) => setContactInfo({...contactInfo, facebook: e.target.value})}
                      value={contactInfo.twitter}
                    />
                  </div>
                  <div className="flex flex-row items-center justify-start mt-2 ml-5">
                    <p className="italic text-left">Instagram</p>
                    <Input
                      classNames="text-start w-full ml-2 border-r-0 border-l-0 border-t-0 bg-dark-tertiary !rounded-none"
                      placeholder="Twitter link"
                      type="text"
                      name="instagram"
                      onBlur={(e) => setContactInfo({...contactInfo, instagram: e.target.value})}
                      onChange={(e) => setContactInfo({...contactInfo, facebook: e.target.value})}
                      value={contactInfo.instagram}
                    />
                  </div>
                </div>
              }
              <div className="my-8">
                { formPage == 4
                ? <button
                    type="submit"
                    className="w-full py-1 text-white rounded-lg bg-primary hover:bg-primary-dark roboto-font border-primary hover:border-primary-dark ballon-right"
                  >
                    Ready
                  </button>
                : <button
                    onClick={(e) => handleSubmit(e, formPage, () => handleFormPages('next'))}
                    className="w-full py-1 text-white rounded-lg bg-primary hover:bg-primary-dark roboto-font border-primary hover:border-primary-dark ballon-right"
                  >
                    Next
                  </button>
                }
                { formPage > 1 &&
                  <button
                    onClick={(e) => handleSubmit(e, formPage, () => handleFormPages('prev'))}
                    className="w-full py-1 mt-2 text-white rounded-lg hover:bg-dark-secondary bg-gray hover:border-dark-tertiary border-gray roboto-font ballon-right"
                  >
                    Back
                  </button>
                }
              </div>
              {response && (<Message msg="the data has been sent correctly" classNames="bg-primary"/> )}
            </form>
          </Card>
        </div>
      </div>

      <div className="hidden py-1 md:block">
        <p className="my-8 text-lg text-left text-gray-400 roboto-font">
           2022 voku.org
        </p>
      </div>
    </div>
  );
}

export default Register;
