import React, { useState, useEffect } from "react";
import {ReactComponent as AVF} from '../../assets/avatar-f.svg'
import {ReactComponent as AVM} from '../../assets/avatar-m.svg'

const Avatar = ({src, classNames, sizes, gender, onClick, ...props}) => {
  

  return (
    <div onClick={onClick} className={`w-fit rounded-full cursor-pointer bg-transparent border-gray ${classNames}`}>
      {src
      ? <img alt="Avatar" className={`rounded-full cursor-pointer bg-transparent border-gray ${sizes} ${classNames}`} title="Avatar_user" src={src} />
      : gender === 'Male'
        ? <AVM className={` ${sizes}`}/>
        : <AVF className={` ${sizes}`}/>
      }
    </div>
  );
}

export default Avatar;