import React, { useEffect, useState, useRef } from 'react';

/**
 * Hook that alerts clicks outside of the passed ref
 */
function useOutsideAlerter(ref, action) {
  useEffect(() => {
    /**
     * Alert if clicked on outside of element
     */
    function handleClickOutside(event) {
      if (ref.current && !ref.current.contains(event.target)) {
        action && action();
      }
    }
    // Bind the event listener
    document.addEventListener("mousedown", handleClickOutside);
    return () => {
      // Unbind the event listener on clean up
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, [ref]);
}

const Popover = ({children, options, className, id}) => {

  const [localOptions, setLocalOptions] = useState([]);
  const [visibility, setVisibility] = useState(false);
  const wrapperRef = useRef(null);
  useOutsideAlerter(wrapperRef, () => setVisibility(false));

  const handleModal = () => {
    setVisibility(prevVisibility => !prevVisibility);
  }

  useEffect(() => {
    if(options && options.length > 0)
    {
      setLocalOptions(options);
    }
  }, []);

  return (
    <div className='relative select-none'>
      <div onClick={() => handleModal()} className={`text-white cursor-pointer hover:bg-gray focus:ring-4 font-medium rounded-t-lg md:rounded-lg text-sm px-5 py-2.5 text-center ${className}`}>
        {children}
      </div>
      <div ref={wrapperRef} id={id} className={`${!visibility ? 'hidden' : ''} z-40 block md:absolute md:top-14 md:right-0`}>
          {localOptions.map((item, index) => {
            return <div key={`pop-${id}-${index}`} className={`w-fit h-fit px-3 md:px-2 p-3 text-white border hover:border-gray border-dark-tertiary ${index == 0 ? 'md:rounded-t-lg' : index = localOptions.length -1 ? 'rounded-b-lg' : ''} cursor-pointer h-fit hover:bg-gray bg-dark-tertiary`} onClick={item.onClick}>{item.text}</div>
          })}
      </div>
    </div>
  );
}

export default Popover;